import {
	EditOutlined,
	LocationOnOutlined,
	ManageAccountsOutlined,
	WorkOutlineOutlined,
} from "@mui/icons-material"
import { Box, Divider, Typography, useTheme } from "@mui/material"
import FlexBetween from "components/FlexBetween"
import UserImage from "components/UserImage"
import WidgetWrapper from "components/WidgetWrapper"
import { useEffect, useState } from "react"
import { useSelector } from "react-redux"
import { useLocation, useNavigate } from "react-router-dom"
  
const UserWidget = ({ userId, picturePath }) => {
	const [user, setUser] = useState(null);
	const { palette } = useTheme();
	const navigate = useNavigate();
	const token = useSelector((state) => state.token);
	const signedInUserId = useSelector((state) => state.user._id)
	const loc = useLocation()
	const dark = palette.neutral.dark;
	const medium = palette.neutral.medium;
	const main = palette.neutral.main;
	
	const getUser = async () => {
	  const response = await fetch(`${process.env.SERVERSIDE_URL}/users/${userId}`, {
		method: "GET",
		headers: { Authorization: `Bearer ${token}` },
	  });
	  const data = await response.json();
	  setUser(data);
	};
  
	useEffect(() => {
	  getUser();
	}, []); // eslint-disable-line react-hooks/exhaustive-deps
  
	if (!user) {
	  return null;
	}
  
	const {
	  firstName,
	  lastName,
	  location,
	  occupation,
	  viewedProfile,
	  impressions,
	  friends,
	} = user;
  
	return (
	  <WidgetWrapper>
		{/* FIRST ROW */}
		<FlexBetween
		  gap="0.5rem"
		  pb="1.1rem"
		  onClick={() => navigate(`/profile/${userId}`)}
		>
		  <FlexBetween gap="1rem">
			<UserImage image={picturePath} />
			<Box>
			  <Typography
				variant="h4"
				color={dark}
				fontWeight="500"
				sx={{
				  "&:hover": {
					color: palette.primary.light,
					cursor: "pointer",
				  },
				}}
			  >
				{firstName} {lastName}
			  </Typography>
			  <Typography color={medium}>{friends.length} </Typography>
			</Box>
		  </FlexBetween>

		  {loc.pathname === `/profile/${signedInUserId}` && <ManageAccountsOutlined />}

		</FlexBetween>
  
		<Divider />
  
		{/* SECOND ROW */}
		<Box p="1rem 0">
		  <Box display="flex" alignItems="center" gap="1rem" mb="0.5rem">
			<LocationOnOutlined fontSize="large" sx={{ color: main }} />
			<Typography color={medium}>{location}</Typography>
		  </Box>
		  <Box display="flex" alignItems="center" gap="1rem">
			<WorkOutlineOutlined fontSize="large" sx={{ color: main }} />
			<Typography color={medium}>{occupation}</Typography>
		  </Box>
		</Box>
  
		<Divider />
  
		{/* THIRD ROW */}
		<Box p="1rem 0">
		  <FlexBetween mb="0.5rem">
			<Typography color={medium}>   </Typography>
			<Typography color={main} fontWeight="500">
			  {viewedProfile}
			</Typography>
		  </FlexBetween>
		  <FlexBetween>
			<Typography color={medium}>  </Typography>
			<Typography color={main} fontWeight="500">
			  {impressions}
			</Typography>
		  </FlexBetween>
		</Box>
  
		<Divider />
  
		{/* FOURTH ROW */}
		<Box p="1rem 0">
		  <Typography fontSize="1rem" color={main} fontWeight="500" mb="1rem">
			
		  </Typography>
  
		  <FlexBetween gap="1rem" mb="0.5rem">
			<FlexBetween gap="1rem">
			  <img src="../assets/twitter.png" alt="twitter" />
			  <Box>
				<Typography color={main} fontWeight="500">
				  Twitter
				</Typography>
				<Typography color={medium}></Typography>
			  </Box>
			</FlexBetween>

			{loc.pathname === `/profile/${signedInUserId}` && 
			<EditOutlined sx={{ color: main }} />}

		  </FlexBetween>
  
		  <FlexBetween gap="1rem">
			<FlexBetween gap="1rem">
			  <img src="../assets/linkedin.png" alt="linkedin" />
			  <Box>
				<Typography color={main} fontWeight="500">
				  Linkedin
				</Typography>
				<Typography color={medium}></Typography>
			  </Box>
			</FlexBetween>

			{loc.pathname === `/profile/${signedInUserId}` && 
			<EditOutlined sx={{ color: main }} />}
			
		  </FlexBetween>
		</Box>
	  </WidgetWrapper>
	);
};
  
export default UserWidget;